.ControlPanel {
  height: 30vh;
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.05);
}

.ControlPanel-Center {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* BUTTONS... */

.btn-control {
  width: 10em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.btn-control-passfail {
  margin: 3em;
  height: 50px;
  width: 100px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (max-width: 600px) {
  .ControlPanel-Center {
    font-size: 0.8em;
  }

  .btn-control-passfail {
    margin: 0.5em;
    height: auto;
    width: auto;
  }
}
